import React, { useState } from "react";
import styled from "styled-components";
import List from "@material-ui/core/List";
import Item from "./Item";
import Modal from "./Modal";
import Pagination from '@material-ui/lab/Pagination';
import { withStyles } from "@material-ui/styles";

const ListView = ({ items }) => {
  const [isModalOpen, setIsModalOpen] = useState(false);

  const onItemClick = () => {
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
  }

  return (
    <>
    <ListViewStyle>
      <ListBody>
        <Fields>
          <Status>상태</Status>
          <Title>사업명</Title>
          <Company>발주업체명</Company>
          <StartedAt>입찰개시일</StartedAt>
          <EndedAt>투찰마감일</EndedAt>
          <Type>공고구분</Type>
        </Fields>

        <List>
          {Object.keys(items).map((key) => (
            <Item key={key} item={items[key]} onClick={onItemClick} />
          ))}
        </List>
      </ListBody>
      <StyledPagination count={10} shape="rounded" />
    </ListViewStyle>
      {isModalOpen && <Modal closeModal={closeModal} />}
      </>
  );
};

const StyledPagination = withStyles({
  root: {
    height: 30,
    display: 'flex',
    justifyContent:'center',
    alignItems:'center',
    margin: 10,
  }
})(Pagination);

const ListViewStyle = styled.section`
  height:100%;
  display:flex;
  flex-direction:column;
  align-items:center;
  
`;

const ListBody = styled.header`
  width: 100%;
  background-color: #f7f6f8;
  flex: 1;
  padding: 20px 30px;
`;

const Fields = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  font-weight: bold;
  font-size: 14px;
`;
const Status = styled.h1`
  padding-left: 15px;
  width: 5%;
  min-width: 60px;
`;
const Title = styled.h1`
  padding-left: 15px;
  width: 30%;
`;
const Company = styled.h1`
  padding-left: 15px;
  width: 25%;
`;
const StartedAt = styled.h1`
  padding-left: 15px;
  width: 10%;
  min-width: 150px;
`;
const EndedAt = styled.h1`
  padding-left: 15px;
  width: 10%;
  min-width: 150px;
`;
const Type = styled.h1`
  padding-left: 15px;
  width: 20%;
`;
export default ListView;
