import React from "react";
import styled from "styled-components";
import { withStyles } from "@material-ui/core/styles";
import { ListItem } from "@material-ui/core";
import Chip from "@material-ui/core/Chip";

const Item = ({ item, onClick }) => {
  const { status, title, company, bid_started_at, bid_ended_at, type } = item;

  return (
    <div onClick={onClick}>
      <StyledListItem button theme={{ main: status === "READ" ? 0.5 : 1 }}>
        <Status>{status === "NEW" ? "새 공고" : (status === "READ" ? "읽음" : "안읽음")}</Status>
        <Title>{title}</Title>
        <Company>{company}</Company>
        <StartedAt>{bid_started_at}</StartedAt>
        <EndedAt>{bid_ended_at}</EndedAt>
        <Type>{type && type.map((value) => <StyledChip label={value} />)}</Type>
      </StyledListItem>
    </div>
  );
};

const StyledListItem = withStyles({
  root: {
    background: "white",
    borderRadius: 2,
    border: "1px solid #c6c6c6",
    color: "black",
    minHeight: 50,
    height: 'auto',
    padding: 0,
    fontSize: "16px",
    marginBottom: "10px",
    opacity: (props) => props.theme.main,
  },
})(ListItem);

const StyledChip = withStyles({
  root: {
    margin: "5px 10px 5px 0",
    backgroundColor: "#7A37D8",
    color: "white",
    borderRadius: 2,
  },
})(Chip);

const Status = styled.h1`
  padding-left: 15px;
  width: 5%;
  min-width: 60px;
`;
const Title = styled.h1`
  padding-left: 15px;
  width: 30%;
`;
const Company = styled.h1`
  padding-left: 15px;
  width: 25%;
`;
const StartedAt = styled.h1`
  padding-left: 15px;
  width: 10%;
  min-width: 150px;
`;
const EndedAt = styled.h1`
  padding-left: 15px;
  width: 10%;
  min-width: 150px;
`;
const Type = styled.h1`
  padding-left: 15px;
  width: 20%;
`;

export default Item;
